import React, { Component, Fragment } from "react";
import "./index.css";
import { S, wait, ProgressBar } from "./alabsi";
import Diary from "./Diary";
import { updateRenderState } from ".";
import SearchPage from "./SearchPage";
import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { userData, data } from "./Data";

gsap.registerPlugin(ScrollToPlugin);

let isReversed = false;
let AddIsPressable = true;
let tl;
let arrowIsPressable = true;
export let dateIndex = 0;
export function dateGetter(i) {
	const days = ["الأحد", "الاثنين", "الثلاثاء", "الاربعاء", "الخميس", "الجمعة", "السبت"];
	const newDate = new Date();
	const newDecrementDate = new Date(newDate.getFullYear(), newDate.getMonth(), newDate.getDate() + i);
	const getYear = newDecrementDate.getFullYear();
	const getMonth = newDecrementDate.getMonth() + 1;
	const getDate = newDecrementDate.getDate();
	const getDay = newDecrementDate.getDay();
	return `${getYear} / ${getMonth} / ${getDate} , ${days[getDay]}`;
}

class MainPage extends Component {
	constructor() {
		super();
		this.state = {
			date: dateIndex === 0 ? "اليوم" : dateIndex === 1 ? "الغد" : dateIndex === -1 ? "البارحة" : dateGetter(dateIndex)
		};
	}

	componentDidMount() {
		if (this.props.doAnimation) this.dateSelectorIntroAnimation();

		this.introPageTransition();

		this.responsiveResizing();

		window.addEventListener("resize", this.responsiveResizing);
		window.addEventListener("scroll", this.hideHeaderScrollAnimation);

		this.setRemainPercent();
	}

	componentDidUpdate() {
		this.setRemainPercent();
	}

	componentWillUnmount() {
		window.removeEventListener("scroll", this.hideHeaderScrollAnimation);
		window.removeEventListener("resize", this.responsiveResizing);
		window.removeEventListener("resize", this.resizeProgressHandler);
	}

	NutritionSum = n => {
		const getDataByDate = userData.diary.filter(e => e.date === dateGetter(dateIndex))[0];
		let sum = 0;
		if (getDataByDate) {
			for (let i in getDataByDate) {
				if (i !== "date") {
					const getDataById = getDataByDate[i].map(
						e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
					);

					sum += getDataById.map(e => e[n] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
				}
			}
		}
		return sum;
	};

	remainKcalSum = _ => ~~(userData.userInfo.dailykcal - this.NutritionSum("kcal"));
	eatenKcalPercent = _ => Math.round((this.NutritionSum("kcal") / userData.userInfo.dailykcal) * 100);

	allCarbSum = _ => ~~this.NutritionSum("carb");
	carbPercent = _ => ~~((this.NutritionSum("carb") / userData.userInfo.dailyCarbs) * 100);

	allFatSum = _ => ~~this.NutritionSum("fat");
	fatPercent = _ => ~~((this.NutritionSum("fat") / userData.userInfo.dailyFat) * 100);

	allCProteinSum = _ => ~~this.NutritionSum("protein");
	proteinPercent = _ => ~~((this.NutritionSum("protein") / userData.userInfo.dailyProtein) * 100);

	diarySlideAnimation = async diraction => {
		if (arrowIsPressable) {
			arrowIsPressable = false;
			const clonedEl = S("#pageBody").node.cloneNode(true);
			if (diraction === "toLeft") {
				S("#diaryContainer").node.prepend(clonedEl);
				gsap.to("#diaryContainer", { duration: 0.3, scrollTo: { x: "0" } });
			} else if (diraction === "toRight") {
				S("#diaryContainer").node.append(clonedEl);
				gsap.fromTo("#diaryContainer", { scrollTo: { x: "0" } }, { duration: 0.3, scrollTo: { x: "max" } });
			}
			await wait(300);
			diraction === "toRight" ? S("#diaryContainer > div:last-child").node.remove() : S("#diaryContainer > div:first-child").node.remove();
			arrowIsPressable = true;
		}
	};

	dateSelectorIntroAnimation = async _ => {
		const getMargin =
			S("#dateSelector").getStyle("width", true) / 2 -
			(S("#dateSelector p").getStyle("width", true) / 2 + S("#dateToRightArrow").getStyle("width", true) + 20);
		S("#dateSelector").css("justify-content", "center");
		await wait(200);
		S("#dateSelector p").css("margin", "20px " + getMargin + "px");
		await wait(500);
		S("#dateSelector").removeAttr("style");
		S("#dateSelector p").removeAttr("style");
	};

	checkDate = (decrement = false) => {
		if (arrowIsPressable) {
			decrement ? dateIndex-- : dateIndex++;
			if (dateIndex === 0) {
				this.setState({ date: "اليوم" });
			} else if (dateIndex === 1) {
				this.setState({ date: "الغد" });
			} else if (dateIndex === -1) {
				this.setState({ date: "البارحة" });
			} else {
				this.setState({ date: dateGetter(dateIndex) });
			}
			decrement ? this.diarySlideAnimation("toRight") : this.diarySlideAnimation("toLeft");
		}
	};

	mountSearchComponent = (title, hash) => {
		isReversed = false;
		updateRenderState(<SearchPage key={Math.random() * 100} title={title} doAnimation={true} />);
		window.history.pushState("", "", "/search#" + hash);
	};

	introPageTransition = async _ => {
		await wait(20);
		S(".MainPage").css("opacity", "1");
	};

	hideHeaderScrollAnimation = _ => {
		const getHeaderHeight = S("#header").getStyle("height", true);
		let multiplier = (1 / (getHeaderHeight / 1.2)) * (getHeaderHeight / 1.2 - window.scrollY);
		multiplier = multiplier < 0 ? 0 : multiplier;
		S(".HideOnScrollElements")
			.css("transform", `scale(${multiplier})`)
			.css("opacity", multiplier);
	};

	responsiveResizing = _ => {
		// for the header sticky postion from the top
		const getHeaderHeight = -S("#header").getStyle("height", true) + 55 + "px";
		S("#header").css("top", getHeaderHeight);

		// for add buttons postion from the left
		const getPageMargin = ((S(".MainPage").getStyle("margin-left", true) + 45) * 100) / window.innerWidth + "%";
		if (!isReversed) S(".floatButton").css("left", getPageMargin);

		// for macros percentage bars position from the bottom
		const getHeaderTitleHeight = S("#title").getStyle("height", true);
		const getHeaderPercentNumHeight = S(".percentNum").getStyle("height", true);
		const calcBottom = getHeaderTitleHeight + getHeaderPercentNumHeight + 38 + "px";
		S("#macrosPercent").css("bottom", calcBottom);
	};

	animateAddFoodButton = async _ => {
		const animDuration = 0.15;
		if (!isReversed && AddIsPressable) {
			AddIsPressable = false;
			tl = gsap.timeline();
			tl.to("#addFoodBG", { duration: animDuration, opacity: 1, display: "block" }, "a");
			tl.to("#addButton svg", { duration: animDuration, rotate: 135 }, "b");
			tl.to("#addButton", { duration: animDuration, scale: 1.03, backgroundColor: "#36b078" }, "b");
			tl.to("#addButton", { duration: animDuration, scale: 1, backgroundColor: "#41cd8c" }, "c");
			tl.to("#addSnacks", { duration: animDuration, display: "block", bottom: "calc(50% - 75px)", left: "50%", scale: 1, delay: 0.1 }, "b");
			tl.to("#addDinner", { duration: animDuration, display: "block", bottom: "50%", left: "calc(50% - 75px)", scale: 1, delay: 0.2 }, "b");
			tl.to("#addLaunch", { duration: animDuration, display: "block", bottom: "50%", left: "calc(50% + 75px)", scale: 1, delay: 0.3 }, "b");
			tl.to("#addBreakfast", { duration: animDuration, display: "block", bottom: "calc(50% + 75px)", left: "50%", scale: 1 }, "b");
			isReversed = true;
			await wait(600);
			AddIsPressable = true;
		} else if (isReversed && AddIsPressable) {
			this.responsiveResizing();
			AddIsPressable = false;
			tl.reverse();
			isReversed = false;
			await wait(600);
			AddIsPressable = true;
		}
	};

	setRemainPercent = _ => {
		let getWidth = S("#header").getStyle("width", true) / 3 / 2;
		ProgressBar("#remainProgress", {
			input: this.eatenKcalPercent() > 100 ? this.eatenKcalPercent() - 100 : this.eatenKcalPercent(),
			R: getWidth,
			width: 5,
			fillColor: "none",
			backgroundColor: this.eatenKcalPercent() > 100 ? "grad" : "rgb(250,250,245,0.3)",
			textColor: this.eatenKcalPercent() > 100 ? "#F3D737" : "white",
			beforeText: this.eatenKcalPercent() > 100 ? "+ " : "",
			afterText: "%",
			progressColor: this.eatenKcalPercent() > 100 ? "#F3D737" : "grad",
			gradColor1: "#FFFEFF",
			gradColor2: "#D7FFFE",
			doAnimation: this.props.doAnimation ? "yes" : "no"
		});

		window.addEventListener("resize", this.resizeProgressHandler);
	};

	resizeProgressHandler = _ => {
		const getWidth = S("#header").getStyle("width", true) / 3 / 2;
		const input = this.eatenKcalPercent() > 100 ? this.eatenKcalPercent() - 100 : this.eatenKcalPercent();
		S("#remainProgress svg")
			.attr("height", getWidth * 2 + 8)
			.attr("width", getWidth * 2 + 8);
		S("#remainProgress svg circle")
			.attr("r", getWidth)
			.attr("cx", getWidth + 4)
			.attr("cy", getWidth + 4);
		S("#remainProgress svg text")
			.css("font-size", getWidth / 2 + "px")
			.attr("dy", getWidth / 6);
		const calcProgress = 2 * Math.PI * getWidth - (input * (2 * Math.PI * getWidth)) / 100;
		S("#remainProgress svg circle:nth-child(3)")
			.css("stroke-dashoffset", calcProgress + "px")
			.css("stroke-dasharray", 2 * Math.PI * getWidth + "px");
	};

	render() {
		return (
			<Fragment>
				<div id='addFoodBG' onClick={this.animateAddFoodButton}></div>
				<div className='MainPage'>
					<h2 id='title'>حاسبة الطعام</h2>
					<div id='header'>
						<svg width='100%' viewBox='0 0 320 300' fill='none'>
							<linearGradient id='Gradient1' gradientTransform='rotate(45)'>
								<stop offset='20%' />
								<stop offset='120%' />
							</linearGradient>
							<path d='M0 0H320V286.491C320 286.491 249 300 159 300C69 300 0 286.491 0 286.491V0Z' fill='url(#Gradient1)' />
						</svg>
						<div className='remainValues'>
							<div className='caloriesEaten'>
								<p className='HideOnScrollElements'>{~~this.NutritionSum("kcal")}</p>
								<p className='HideOnScrollElements'>الـمـأكـولة</p>
							</div>
							<div id='remainProgress' className='HideOnScrollElements'></div>
							<div className='caloriesRemain'>
								<p className='HideOnScrollElements' style={{ color: this.remainKcalSum() < 0 ? "#F3D737" : "white" }}>
									{this.remainKcalSum() < 0 ? this.remainKcalSum() * -1 : this.remainKcalSum()}
								</p>
								<p className='HideOnScrollElements'>{this.remainKcalSum() < 0 ? "زائــدة" : "المتبـقية"}</p>
							</div>
						</div>
						<div id='macrosPercent'>
							<div className='macrosPercentContainer HideOnScrollElements'>
								<p className='macroName'>الكربوهيدرات</p>
								<div
									className='macrosPercentLine'
									id='macrosPercentLineCarbs'
									style={{ width: Math.min(Math.max(this.carbPercent(), 0), 100) + "%" }}
								></div>
								<div
									className='macrosPercentLine'
									style={{
										backgroundColor: "#F3D737",
										backgroundImage: "initial",
										width: (this.carbPercent() > 100 ? Math.min(Math.max(this.carbPercent() - 100, 0), 100) : 0) + "%"
									}}
								></div>
								<p className='percentNum'>
									<span style={{ display: userData.userInfo.dailyCarbs - this.allCarbSum() < 0 ? "none" : "initial" }}>باقي </span>
									{userData.userInfo.dailyCarbs - this.allCarbSum() < 0
										? (userData.userInfo.dailyCarbs - this.allCarbSum()) * -1
										: userData.userInfo.dailyCarbs - this.allCarbSum()}
									<span>غ</span>
									<span style={{ display: userData.userInfo.dailyCarbs - this.allCarbSum() < 0 ? "initial" : "none" }}> زائدة</span>
								</p>
							</div>
							<div className='macrosPercentContainer HideOnScrollElements'>
								<p className='macroName'>البروتين</p>
								<div
									className='macrosPercentLine'
									id='macrosPercentLineProtein'
									style={{ width: Math.min(Math.max(this.proteinPercent(), 0), 100) + "%" }}
								></div>
								<div
									className='macrosPercentLine'
									style={{
										backgroundColor: "#F3D737",
										backgroundImage: "initial",
										width: (this.proteinPercent() > 100 ? Math.min(Math.max(this.proteinPercent() - 100, 0), 100) : 0) + "%"
									}}
								></div>
								<p className='percentNum'>
									<span style={{ display: userData.userInfo.dailyProtein - this.allCProteinSum() < 0 ? "none" : "initial" }}>باقي </span>
									{userData.userInfo.dailyProtein - this.allCProteinSum() < 0
										? (userData.userInfo.dailyProtein - this.allCProteinSum()) * -1
										: userData.userInfo.dailyProtein - this.allCProteinSum()}
									<span>غ</span>
									<span style={{ display: userData.userInfo.dailyProtein - this.allCProteinSum() < 0 ? "initial" : "none" }}> زائدة</span>
								</p>
							</div>
							<div className='macrosPercentContainer HideOnScrollElements'>
								<p className='macroName'>الدهون</p>
								<div
									className='macrosPercentLine'
									id='macrosPercentLineFat'
									style={{ width: Math.min(Math.max(this.fatPercent(), 0), 100) + "%" }}
								></div>
								<div
									className='macrosPercentLine'
									style={{
										backgroundColor: "#F3D737",
										backgroundImage: "initial",
										width: (this.fatPercent() > 100 ? Math.min(Math.max(this.fatPercent() - 100, 0), 100) : 0) + "%"
									}}
								></div>
								<p className='percentNum'>
									<span style={{ display: userData.userInfo.dailyFat - this.allFatSum() < 0 ? "none" : "initial" }}>باقي </span>
									{userData.userInfo.dailyFat - this.allFatSum() < 0
										? (userData.userInfo.dailyFat - this.allFatSum()) * -1
										: userData.userInfo.dailyFat - this.allFatSum()}
									<span>غ</span>
									<span style={{ display: userData.userInfo.dailyFat - this.allFatSum() < 0 ? "initial" : "none" }}> زائدة</span>
								</p>
							</div>
						</div>
					</div>
					<div id='dateSelector'>
						<svg
							id='dateToRightArrow'
							className='dateArrow'
							xmlns='http://www.w3.org/2000/svg'
							width='24'
							height='24'
							viewBox='0 0 24 24'
							onClick={_ => this.checkDate(true)}
						>
							<path d='M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z' />
						</svg>
						<p>{this.state.date}</p>
						<svg
							id='dateToLeftArrow'
							className='dateArrow'
							xmlns='http://www.w3.org/2000/svg'
							width='24'
							height='24'
							viewBox='0 0 24 24'
							transform='rotate(180)'
							onClick={_ => this.checkDate(false)}
						>
							<path d='M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z' />
						</svg>
					</div>
					<div id='diaryContainer'>
						<Diary date={dateGetter(dateIndex)} />
					</div>
					<div id='addButton' onClick={this.animateAddFoodButton}>
						<svg width='24' height='24' viewBox='0 0 24 24'>
							<path d='M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z' fill='white' />
						</svg>
					</div>
					<div id='addBreakfast' className='circleButton floatButton' onClick={_ => this.mountSearchComponent("الفطور", "breakfast")}>
						<img src='./assets/bread.png' alt='bread' />
						<p>الفطور</p>
					</div>
					<div id='addLaunch' className='circleButton floatButton' onClick={_ => this.mountSearchComponent("الغداء", "lunch")}>
						<img src='./assets/soup.png' alt='soup' />
						<p>الغداء</p>
					</div>
					<div id='addDinner' className='circleButton floatButton' onClick={_ => this.mountSearchComponent("العشاء", "dinner")}>
						<img src='./assets/fish.png' alt='fish' />
						<p>العشاء</p>
					</div>
					<div id='addSnacks' className='circleButton floatButton' onClick={_ => this.mountSearchComponent("وجبة خفيفة", "snacks")}>
						<img src='./assets/banana.png' alt='banana' />
						<p>وجبة خفيفة</p>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default MainPage;
