import React, { Component } from "react";
import MainPage from "./MainPage";
import NavicationBar from "./NavicationBar";
import { updateRenderState } from ".";
import { S, wait } from "./alabsi";
import CategoriesList from "./CategoriesList";
import { data, userData } from "./Data";
import ItemCard from "./ItemCard";
import FavoritePage from "./FavoritePage";
import RecentlyAdded from "./RecentlyAdded";

let tmpSearchInput = "";
export default class SearchPage extends Component {
	state = {
		searchData: "",
		favoritesData: ""
	};

	componentDidMount() {
		if (this.props.doAnimation) {
			this.introPageTransition();
		} else {
			this.introPageFadeTransition();
		}
		S("#searchBar").node.value = tmpSearchInput;
		this.searchOnChangeHandle();
		window.addEventListener("popstate", _ => {
			if (window.location.pathname === "/") tmpSearchInput = "";
		});
	}

	searchOnChangeHandle = _ => {
		const searchInput = document.getElementById("searchBar").value;
		tmpSearchInput = searchInput;
		searchInput.length > 0 ? S("#searchOption").css("display", "none") : S("#searchOption").removeAttr("style");
		if (searchInput.length === 0) S("#searchNoResults").css("display", "none");
		if (searchInput.length > 1) {
			const searchData = data.filter(e => e.name.toLowerCase().indexOf(searchInput.toLowerCase()) !== -1);
			const favoritedata = userData.favorite.food.map(e => (e = Object.assign({}, data.filter(el => el.id === e)[0])));
			const favoriteSearchdata = favoritedata.filter(e => e.name.toLowerCase().indexOf(searchInput.toLowerCase()) !== -1);
			searchData.length === 0 ? S("#searchNoResults").css("display", "block") : S("#searchNoResults").removeAttr("style");
			this.setState({
				searchData: searchData.map(e => (
					<ItemCard
						key={e.id}
						name={e.name}
						kcal={e.serving ? ~~((e.kcal / 100) * e.serving[0][1]) : e.kcal}
						serving={e.serving ? e.serving[0][0] : "كل (100) غرام"}
						id={e.id}
						description={e.description}
					/>
				)),
				favoritesData: favoriteSearchdata.map(e => (
					<ItemCard
						key={e.id}
						name={e.name}
						kcal={e.serving ? ~~((e.kcal / 100) * e.serving[0][1]) : e.kcal}
						serving={e.serving ? e.serving[0][0] : "كل (100) غرام"}
						id={e.id}
						description={e.description}
					/>
				))
			});
		} else if (searchInput.length < 3) {
			this.setState({ searchData: "", favoritesData: "" });
		}
	};

	introPageTransition = async _ => {
		S("#SearchPage")
			.css("position", "fixed")
			.css("bottom", "-100vh")
			.css("left", "50%")
			.css("opacity", "0")
			.css("transform", "translate(-50%, 100%)");
		S("#searchPageHeader").css("position", "relative");
		await wait(10);
		S("#SearchPage")
			.css("bottom", "100%")
			.css("opacity", "1");
		await wait(S("#SearchPage").getStyle("transition-duration", true) * 1000);
		S("#SearchPage").removeAttr("style");
		S("#searchPageHeader").removeAttr("style");
	};

	introPageFadeTransition = async _ => {
		S("#SearchPage")
			.css("opacity", 0)
			.css("transition", "opacity 350ms");
		await wait(20);
		S("#SearchPage").css("opacity", 1);
		await wait(350);
		S("#SearchPage").removeAttr("style");
	};

	backButtonHandle = _ => {
		tmpSearchInput = "";
		updateRenderState([<MainPage key={Math.random() * 100} doAnimation={true} />, <NavicationBar key={Math.random() * 100} />]);
		window.history.pushState("", "", "/");
	};

	favoriteButtonHandle = _ => {
		updateRenderState(<FavoritePage key={Math.random() * 100} doAnimation={true} />);
		window.history.pushState("", "", "/favorites#" + window.location.hash.split("#")[1]);
	};

	recentButtonHandle = _ => {
		updateRenderState(<RecentlyAdded key={Math.random() * 100} doAnimation={true} />);
		window.history.pushState("", "", "/recentlyAdded#" + window.location.hash.split("#")[1]);
	};

	categoriesButtonHandle = _ => {
		updateRenderState(<CategoriesList key={Math.random() * 100} />);
		window.history.pushState(
			"",
			"",
			"/search" + window.location.hash + (window.location.hash.includes("#categoriesList") ? "" : "#categoriesList")
		);
	};

	checkHash = _ => {
		let hash;
		switch (window.location.hash.split("#")[1]) {
			case "breakfast":
				hash = "الفطور";
				break;
			case "lunch":
				hash = "الغداء";
				break;
			case "dinner":
				hash = "العشاء";
				break;
			case "snacks":
				hash = "وجبة خفيفة";
				break;
			default:
				break;
		}
		return hash;
	};
	render() {
		return (
			<div id='SearchPage'>
				<header id='searchPageHeader'>
					<div id='searchPageHeaderTopContainer'>
						<p id='searchPageHeaderTitle'>{this.checkHash()}</p>
						<svg id='searchPageBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={this.backButtonHandle}>
							<path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
						</svg>
					</div>
					<input id='searchBar' placeholder='بحث' autoComplete='off' type='search' onChange={this.searchOnChangeHandle} />
				</header>
				<div id='searchResultsfavorite'>
					<p className='searchResultsTitles'>{this.state.favoritesData.length > 0 ? "المفضلة" : ""}</p>
					{this.state.favoritesData}
				</div>
				<div id='searchResults'>
					<p className='searchResultsTitles'>{this.state.searchData.length > 0 ? "النتائج" : ""}</p>
					{this.state.searchData}
				</div>
				<p id='searchNoResults'>لا يوجد نتائج</p>
				<div id='searchOption'>
					<p onClick={this.categoriesButtonHandle}>
						<svg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'>
							<path d='M21.639 14l-1.333 8h-16.612l-1.333-8h19.278zm2.361-2h-24l2 12h20l2-12zm-19.226-2l2.667-8h9.117l2.667 8h2.107l-3.332-10h-12l-3.333 10h2.107zm3.248-5l-.333 1h8.621l-.333-1h-7.955zm-.667 2l-.333 1h9.955l-.333-1h-9.289zm10.29 3l-.333-1h-10.624l-.333 1h11.29zm-8.956-7l-.333 1h7.288l-.334-1h-6.621zm6.311 13h-6v2h6v-2z' />
						</svg>
						الأصناف
					</p>
					<p onClick={this.recentButtonHandle}>
						<svg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'>
							<path d='M11 6v8h7v-2h-5v-6h-2zm-8.4 9.397l-1.799.906c-.302-.784-.521-1.607-.652-2.46l1.998-.159c.1.59.253 1.162.453 1.713zm4.393 7.501l.669-1.899c-.549-.265-1.068-.577-1.555-.933l-1.413 1.443c.708.545 1.478 1.012 2.299 1.389zm-5.319-4.795c.44.741.956 1.43 1.539 2.058l1.404-1.433c-.431-.471-.814-.982-1.149-1.528l-1.794.903zm10.322-18.103c-2.79 0-5.349.964-7.385 2.562l-2.183-2.183-1.356 7.013 7.015-1.354-2.05-2.049c1.666-1.245 3.724-1.989 5.959-1.989 5.516 0 10.003 4.486 10.003 10s-4.487 10-10.003 10c-.848 0-1.668-.118-2.455-.317l-.665 1.894c.996.267 2.039.423 3.12.423 6.629 0 12.004-5.373 12.004-12s-5.375-12-12.004-12zm-11.996 11.849l2.015-.161c.027-.703.125-1.386.288-2.047h-2.076c-.142.714-.217 1.453-.227 2.208z' />
						</svg>
						المضافة مؤخراً
					</p>
					<p onClick={this.favoriteButtonHandle}>
						<svg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'>
							<path d='M6.28 3c3.236.001 4.973 3.491 5.72 5.031.75-1.547 2.469-5.021 5.726-5.021 2.058 0 4.274 1.309 4.274 4.182 0 3.442-4.744 7.851-10 13-5.258-5.151-10-9.559-10-13 0-2.676 1.965-4.193 4.28-4.192zm.001-2c-3.183 0-6.281 2.187-6.281 6.192 0 4.661 5.57 9.427 12 15.808 6.43-6.381 12-11.147 12-15.808 0-4.011-3.097-6.182-6.274-6.182-2.204 0-4.446 1.042-5.726 3.238-1.285-2.206-3.522-3.248-5.719-3.248z' />
						</svg>
						المفضلة
					</p>
					<p>
						<svg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'>
							<path d='M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z' />
						</svg>
						إنشاء جديد
					</p>
				</div>
			</div>
		);
	}
}
