import React, { Component } from "react";
import { updateRenderState } from ".";
import SearchPage from "./SearchPage";
import { userData, data } from "./Data";
import ItemCard from "./ItemCard";

export default class Diary extends Component {
	mountSearchComponent = (ar, en) => {
		updateRenderState(<SearchPage key={Math.random() * 100} title={ar} doAnimation={true} />);
		window.history.pushState(undefined, undefined, "/search#" + en);
	};

	mealKcalSum(mealName) {
		const getDataByDate = userData.diary.filter(e => e.date === this.props.date)[0];
		if (getDataByDate && getDataByDate[mealName]) {
			const getDataById = getDataByDate[mealName].map(
				e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
			);
			return getDataById.map(e => ~~(e.kcal * ((e.value * e.input) / 100))).reduce((a, b) => a + b);
		}
	};

	renderMeal(mealName) {
		const getDataByDate = userData.diary.filter(e => e.date === this.props.date)[0];
		if (getDataByDate && getDataByDate[mealName]) {
			const getDataById = getDataByDate[mealName].map(
				e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
			);
			return getDataById.map(e => (
				<ItemCard
					key={e.id + Math.random() * 1000}
					name={e.name}
					kcal={~~((e.kcal / 100) * (e.value * e.input))}
					serving={e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter(el => el[1] === e.value)[0][0]}
					id={e.id}
					description={e.description}
					value={e.value}
					input={e.input}
					mealName={mealName}
				/>
			));
		}
	};

	render() {
		return (
			<div id='pageBody'>
				<div className='mealName'>
					<h3>الفطور</h3>
				</div>
				<div className='mealFoods'>
					<div
						className='mealFoodsImgP'
						onClick={_ => this.mountSearchComponent("الفطور", "breakfast")}
						style={{ display: this.renderMeal("breakfast") ? "none" : "block" }}
					>
						<img src='./assets/bread.png' alt='bread' />
						<p className='addYourFoodText'>أضف طعامك</p>
					</div>
					{this.renderMeal("breakfast")}
					<div className='mealKcalSum' style={{ display: this.renderMeal("breakfast") ? "block" : "none" }}>
						<p>{this.mealKcalSum("breakfast")} سعرة حرارية</p>
					</div>
				</div>
				<div className='mealName'>
					<h3>الغداء</h3>
				</div>
				<div className='mealFoods'>
					<div
						className='mealFoodsImgP'
						onClick={_ => this.mountSearchComponent("الغداء", "lunch")}
						style={{ display: this.renderMeal("lunch") ? "none" : "block" }}
					>
						<img src='./assets/soup.png' alt='soup' />
						<p className='addYourFoodText'>أضف طعامك</p>
					</div>
					{this.renderMeal("lunch")}
					<div className='mealKcalSum' style={{ display: this.renderMeal("lunch") ? "block" : "none" }}>
						<p>{this.mealKcalSum("lunch")} سعرة حرارية</p>
					</div>
				</div>
				<div className='mealName'>
					<h3>العشاء</h3>
				</div>
				<div className='mealFoods'>
					<div
						className='mealFoodsImgP'
						onClick={_ => this.mountSearchComponent("العشاء", "dinner")}
						style={{ display: this.renderMeal("dinner") ? "none" : "block" }}
					>
						<img src='./assets/fish.png' alt='fish' />
						<p className='addYourFoodText'>أضف طعامك</p>
					</div>
					{this.renderMeal("dinner")}
					<div className='mealKcalSum' style={{ display: this.renderMeal("dinner") ? "block" : "none" }}>
						<p>{this.mealKcalSum("dinner")} سعرة حرارية</p>
					</div>
				</div>
				<div className='mealName'>
					<h3>وجبة خفيفة</h3>
				</div>
				<div className='mealFoods'>
					<div
						className='mealFoodsImgP'
						onClick={_ => this.mountSearchComponent("وجبة خفيفة", "snacks")}
						style={{ display: this.renderMeal("snacks") ? "none" : "block" }}
					>
						<img src='./assets/banana.png' alt='banana' />
						<p className='addYourFoodText'>أضف طعامك</p>
					</div>
					{this.renderMeal("snacks")}
					<div className='mealKcalSum' style={{ display: this.renderMeal("snacks") ? "block" : "none" }}>
						<p>{this.mealKcalSum("snacks")} سعرة حرارية</p>
					</div>
				</div>
			</div>
		);
	}
}
