import React, { Component, Fragment } from "react";
import { userData, data } from "./Data";
import { dateGetter } from "./MainPage";
import ItemCard from "./ItemCard";
import { updateRenderState } from ".";
import SearchPage from "./SearchPage";
import {S, wait} from "./alabsi"

let mealValueTmp = "";
export default class RecentlyAdded extends Component {
	state = { meal: mealValueTmp.length > 0 ? mealValueTmp : window.location.hash.split("#")[1] };

	componentDidMount() {
        if (this.props.doAnimation) {
            this.introPageTransition()
        } else {
            this.introPageFadeTransition()
        }
		window.addEventListener("popstate", _ => {
			if (window.location.pathname === "/" || window.location.pathname.includes("search")) mealValueTmp = "";
		});
    };

    introPageFadeTransition = async _ => {
		S("#recentPage")
			.css("opacity", 0)
			.css("transition", "opacity 350ms");
		await wait(20);
		S("#recentPage").css("opacity", 1);
		await wait(350);
		S("#recentPage").removeAttr("style");
    };
    
    introPageTransition = async _ => {
		S("#recentPage")
			.css("position", "fixed")
			.css("bottom", "-100vh")
			.css("left", "50%")
			.css("opacity", "0")
			.css("transform", "translate(-50%, 100%)");
		S("#recentHeader").css("position", "relative");
		await wait(10);
		S("#recentPage")
			.css("bottom", "100%")
			.css("opacity", "1");
		await wait(S("#recentPage").getStyle("transition-duration", true) * 1000);
		S("#recentPage").removeAttr("style");
		S("#recentHeader").removeAttr("style");
	};

	getRecentMeal = (mealName, dateIndex) => {
		const getDataByDate = userData.diary.filter(e => e.date === dateGetter(dateIndex))[0];
		if (getDataByDate && getDataByDate[mealName]) {
			const getDataById = getDataByDate[mealName].map(
				e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
			);
			return getDataById.map(e => (
				<ItemCard
					key={e.id + Math.random() * 1000}
					name={e.name}
					kcal={~~((e.kcal / 100) * (e.value * e.input))}
					serving={e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter(el => el[1] === e.value)[0][0]}
					id={e.id}
					description={e.description}
					value={e.value}
					input={e.input}
				/>
			));
		}
	};

	renderRecent = _ => {
		let content = [];
		for (let i = -1; i > -8; i--) {
			if (this.getRecentMeal(this.state.meal, i)) {
				content.push([
					<Fragment key={Math.random() * 1000}>
						<p id='recentDateText'>{dateGetter(i)}</p>
						<div>{this.getRecentMeal(this.state.meal, i)}</div>
					</Fragment>
				]);
			}
		}
		return content;
	};

	selectMealHandle = e => {
		this.setState({ meal: e.target.value });
		mealValueTmp = e.target.value;
	};

	backButtonHandle = _ => {
		mealValueTmp = "";
		updateRenderState([<SearchPage key={Math.random() * 100} title={this.checkHash(window.location.hash.split("#")[1])} />]);
		window.history.pushState("", "", `/search#${window.location.hash.split("#")[1]}`);
	};

	checkHash = h => {
		let hash;
		switch (h) {
			case "breakfast":
				hash = "الفطور";
				break;
			case "lunch":
				hash = "الغداء";
				break;
			case "dinner":
				hash = "العشاء";
				break;
			case "snacks":
				hash = "وجبة خفيفة";
				break;
			default:
				break;
		}
		return hash;
	};

	render() {
		return (
			<div id='recentPage'>
				<header id='recentHeader'>
					<p id='recentHeaderTitle'>المضافة مؤخراً</p>
					<svg id='recentBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={this.backButtonHandle}>
						<path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
					</svg>
				</header>
				<select id='recentSelectMeal' value={this.state.meal} onChange={this.selectMealHandle}>
					<option className='itemViewerSelectMealItems' value='breakfast'>
						الفطور
					</option>
					<option className='itemViewerSelectMealItems' value='lunch'>
						الغداء
					</option>
					<option className='itemViewerSelectMealItems' value='dinner'>
						العشاء
					</option>
					<option className='itemViewerSelectMealItems' value='snacks'>
						وجبة خفيفة
					</option>
				</select>
				<p id='recentNoResults' style={{ display: this.renderRecent().length > 0 ? "none" : "block" }}>
					لا يوجد عناصر مضافة في آخر اسبوع على {this.checkHash(this.state.meal)}
				</p>
				<div>{this.renderRecent()}</div>
			</div>
		);
	}
}
